import React, { useContext, useEffect, useState } from 'react';
import authContext from '../../../ctx/authContext';
import notificationContext from '../../../ctx/notificationContext';
import DashboardCard from '../../../ui/DashboardCard';
import LineChart from '../../../utils/LineChart';
import PieChart from '../../../utils/PieChart';

function Dashboard(props) {
  const {fetchData} = props;
  const authCtx = useContext(authContext);
  const notificationCtx = useContext(notificationContext);
  const [dashboardData, setDashboardData] = useState({});
  const [salesDataCount, setSalesDataCount] = useState(null);
  const [salesDataAmount, setSalesDataAmount] = useState(null);
  const [mostSellingFoods, setMostSellingFoods] = useState(null);

  useEffect(()=>{
    const url = "https://flavourfound.onrender.com/admin/business-summary";
    // const url = "http://localhost:80/admin/business-summary";
    fetchData(url, "GET")
    .then(resData=>{
      if(resData.status === 200){
        setDashboardData(resData.summary)
      }
      else{
        throw Error(resData.msg);
      }
    })
    .catch(err=>{
      notificationCtx.showNotification(true, err.message);
    })
  }, []);

  useEffect(()=>{
    const url = "https://flavourfound.onrender.com/admin/sales-analysis";
    // const url = "http://localhost:80/admin/sales-analysis";
    fetchData(url, "GET")
    .then(resData=>{
      if(resData.status === 200){
        const mostSoldFoods = resData.favouriteFoodsResult;
        const mostSoldFoodsLabels = mostSoldFoods.map(item => item.item_name)
        const mostSoldFoodsCount = mostSoldFoods.map(item => item.item_sold)
        const mostSellingFoodsData = {
          labels: mostSoldFoodsLabels,
          datasets: [
            {
              label: 'Total sold',
              data: mostSoldFoodsCount,
              backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)',
              ],
              borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)',
              ],
              borderWidth: 1,
            },
          ],
        };

        setMostSellingFoods(mostSellingFoodsData);

        const noOfFoodItemsSold = Array(12).fill(0);
        const foodItemsSoldAmount = Array(12).fill(0);
        for(let data of resData.salesResult){
          noOfFoodItemsSold[data._id-1] = data.total_sales_count;
          foodItemsSoldAmount[data._id-1] = data.total_sales_amount;
        }
        
        const labels = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        const dataCount = {
          labels,
          datasets: [
            {
              label: 'No. of items sold in a month',
              data: noOfFoodItemsSold,
              borderColor: 'rgb(255, 99, 132)',
              backgroundColor: 'rgba(255, 99, 132, 0.5)',
            },
          ],
        };
        const dataAmount = {
          labels,
          datasets: [
            {
              label: 'Total sales amount in a month in Rs.',
              data: foodItemsSoldAmount,
              borderColor: 'rgb(255, 99, 132)',
              backgroundColor: 'rgba(255, 99, 132, 0.5)',
            },
          ],
        };
        setSalesDataCount(dataCount);
        setSalesDataAmount(dataAmount);
      }
      else{
        throw Error(resData.msg);
      }
    })
    .catch(err=>{
      notificationCtx.showNotification(true, err.message);
    })
  }, []);

  return (
    <div>
      <h1 className='text-6xl mt-2 mb-12 text-[#484b4bf2] text-center underline sm:text-5xl lg:text-4xl xl:text-3xl xl:mb-6'>Admin Dashboard</h1>

      <h2 className='text-5xl mt-2 mb-7 text-[#484b4bf2] sm:text-4xl lg:text-3xl xl:text-2xl xl:mb-4'>Business Summary</h2>
      <div className='flex flex-wrap gap-x-3'>
        <DashboardCard heading={"Total Registered Users"} value={dashboardData.total_users} iconClass={"bx bxs-user-rectangle"} />
        <DashboardCard heading={"Users With Admin Rights"} value={dashboardData.admin_rights} iconClass={'bx bxs-user-check'} />
        <DashboardCard heading={"Total Orders"} value={dashboardData.total_orders} iconClass={"bx bx-trending-up"} />
        <DashboardCard heading={"Total Sales"} value={dashboardData.total_sales} iconClass={"bx bxs-up-arrow"} />
        <DashboardCard heading={"Sales This Month"} value={dashboardData.sales_this_month} iconClass={'bx bxs-calendar'} />
        <DashboardCard heading={"Total Food Items"} value={dashboardData.food_items_count} iconClass={"bx bxs-bowl-hot"} />
      </div>

      <h2 className='text-2xl mt-2 mb-4 text-[#484b4bf2]'>Sales Analysis</h2>

      <div>
        {salesDataCount !== null && <>
          <h2 className='text-xl mt-4 mb-4 text-[#484b4bf2]'>Total Sales(Numbers)</h2>
          <div className='w-[500px] m-auto max-[600px]:w-[90%]'><LineChart data={salesDataCount} /></div>
          <h2 className='text-xl mt-4 mb-4 text-[#484b4bf2]'>Total Sales(Amount)</h2>
          <div className='w-[500px] m-auto max-[600px]:w-[90%]'><LineChart data={salesDataAmount} /></div>
        </>}
        {mostSellingFoods !== null && <>
          <h2 className='text-xl mt-4 mb-4 text-[#484b4bf2]'>Top 5 Favourite Foods</h2>
          <div className='w-[500px] m-auto max-[600px]:w-[90%]'><PieChart data={mostSellingFoods} /></div>
        </>}
      </div>

    </div>
  )
}

export default Dashboard;