import React from 'react';
import FailedImg from '../../assets/images/failed.png';

function PaymentFailed() {
  return (
    <div className='flex-1 flex flex-col items-center justify-center mx-auto'>
      <img src={FailedImg} alt='Payment Successful' className='h-[400px] xl:h-[200px]'></img>
      <span className='text-7xl mt-8 mb-2 font-semibold sm:text-6xl lg:text-5xl xl:text-3xl'>Payment Failed</span>
      <span className='text-6xl sm:text-5xl lg:text-4xl xl:text-2xl'>Try Again After Some Time!!!</span>
      </div>
  )
}

export default PaymentFailed;
